import { ActionArgs, json } from "@remix-run/node";
import { Form, useLoaderData } from "@remix-run/react";
import { createIdea, getAllIdeas, Idea } from "~/db/idea.server";


type LoaderType = {
  ideas: Idea[];
}

type IntentType = "create" | "delete";

export async function loader() {
  const ideas = await getAllIdeas();
  return json({ ideas });
}

export async function action({ request }: ActionArgs) {
  const formData = await request.formData();
  const intent = formData.get("intent") as IntentType;

  if(intent === "create") {
    const name = formData.get("name") as string;
    const idea = await createIdea(name);
    return json({ intent, idea });
  }
}

export default function Index() {

  const { ideas } = useLoaderData<LoaderType>();

  return (
    <div>
      <div className="max-w-screen-md  mx-auto">
        <h1>Re2 Stack</h1>
        <Form method="post" className="flex gap-3">
          <input name="intent" value="create" type="hidden" />
          <input type="text" name="name" placeholder="Write a idea" />
          <button className="text-gray-900 bg-gradient-to-r from-red-200 via-red-300 to-yellow-200 hover:bg-gradient-to-bl focus:ring-4 focus:outline-none focus:ring-red-100 dark:focus:ring-red-400 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2">Save</button>
        </Form>
        <div className="overflow-x-auto relative shadow-md sm:rounded-lg">
            <table className="w-full text-sm text-left text-gray-500">
                <thead className="text-xs text-gray-700 uppercase bg-gray-50">
                    <tr>
                        <th scope="col" className="py-3 px-6">
                            Product name
                        </th>
                        <th scope="col" className="py-3 px-6">
                            Action
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr className="bg-white border-b">
                        <th scope="row" className="py-4 px-6 font-medium text-gray-900 whitespace-nowrap">
                            Apple MacBook Pro 17"
                        </th>
                        <td className="py-4 px-6">
                            <a href="#" className="font-medium text-blue-600 hover:underline">Edit</a>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

      </div>
    </div>
  );
}
